import auth from '../../common/auth.js';
import md5 from '../../common/md5.js';
import Validate from '../../common/validate.js';
export default {
	data() {
		return {
			loading: false,
			phone: '',
			password: '',
			errorText: '',
			option: null
		};
	},
	onShow() {
		uni.getNetworkType({
			success: function(res) {
				console.log(res.networkType);
				if (res.networkType == 'none') {
					uni.reLaunch({
						url: '/pages/notNetwork/notNetwork'
					});
				}
			}
		});
	},
	onLoad(option) {
		// console.log(mg5);
		this.option = option;
	},
	watch: {
		phone(v) {
			if (v) {
				this.errorText = '';
			}
		},
		password(v) {
			if (v) {
				this.errorText = '';
			}
		}
	},
	methods: {
		defaultHandlerLogin: function() {
			this.$api
				.SystemTime()
				.then(res => {
					console.log(res.data);
					let thisYear = {
						y: new Date(res.data).getFullYear(),
						m: new Date(res.data).getMonth() + 1,
						d: new Date(res.data).getDate()
					};
					uni.setStorage({
						key: 'thisYear',
						data: thisYear,
						success: function() {}
					});
				})
				.catch(err => {});
			if (!this.checkPhone()) {
				return;
			} else if (!this.checkPwd()) {
				return;
			}
			this.loading = true;
			setTimeout(e => {
				this.loading = false;
			}, 1500);
			this.$api
				.Login({
					userName: this.phone,
					password: md5.hexMD5(this.password).toUpperCase()
				})
				.then(res => {
					if (res.code == 0) {
						uni.setStorage({
							key: 'userName',
							data: this.phone,
							success: function() {}
						});
						// console.log(res);
						auth.setToken(res.data.token);
						if (!this.option) {
							uni.navigateBack({
								delta: 1
							});
						} else {
							uni.navigateTo({
								url: '/pages/index/index'
							});
						}

						uni.showToast({
							icon: 'none',
							title: '登录成功',
							duration: 1500
						});
					} else {
						uni.showToast({
							icon: 'none',
							title: res.message,
							duration: 1500
						});
					}
				})
				.catch(err => {});
		},
		// 验证原密码
		checkPhone() {
			if (Validate.isEmpty(this.phone)) {
				this.errorText = '账号不能为空！';
				return false;
			} else {
				return true;
			}
		},
		checkPwd() {
			if (Validate.isEmpty(this.password)) {
				this.errorText = '密码不能为空！';
				return false;
			} else if (!Validate.length(this.password, 6, 20)) {
				this.errorText = '密码为6-20位';
				return false;
			} else {
				return true;
			}
		}
	}
};
